<template>
	 <div class="container-fluid">
		<div class="row" style="height: 20px;"></div>
			<div v-for="board in client.boards[page - 1]" class="row mb-3 justify-content-between">
				<div v-for="(b, i) in board" class="col-sm-6 col-md-6">
					<div class="row" style="max-height: 620px;">
						<div class="col-sm-1 col-md-1">
							<!-- 중요 -->
							<i v-if="b.priority === 1" class="icon-star" style="float: right;color: red;"></i>
							<!-- 장수 -->
							<span class="badge mx-1" style="float: right;">
							{{b.contents.length}}
							</span>
							<!-- TTS -->
							<i v-if="b.contents[0].ttsInfo.length > 0" class="icon-speech" style="float: right;color: green;"></i>
							<!-- 마감임박 -->
							<span
							v-if="getPostingEndCount(b.postingEnd) <= 3"
							class="badge badge-danger badge-pill mx-1"
							style="float: right;">
							D-{{getPostingEndCount(b.postingEnd)}}
							</span>
						</div>
						<div class="col-sm col-md">
							<img class="d-block" style="width: 424px; height: 600px;" :src="b.contentPath + '/' + b.contents[0].fileName" @touchend="showAnnounceDetail(b)" @click="showAnnounceDetail(b)"/>
						</div>
					</div>
				</div>
			</div>
			<div class="row">
				<div v-if="client.boards[page - 2]" class="col-2">
					<button class="btn btn-primary" style="float: left" @click="changePage(page - 1)" @touchend="changePage(page - 1)">
					<i class="icon-arrow-left"></i> 이전</button>
				</div>
				<div v-if="client.boards[page]" class="col-2 offset-8">
					<button class="btn btn-primary" style="float: right;" @click="changePage(page + 1)" @touchend="changePage(page + 1)">
					다음<i class="icon-arrow-right"></i></button>
				</div>
			</div>
			<div :style="'width: 100%; background-color: #1c2125;height: 100%;position: absolute;top: 0;left: 0;display:'+showPopup">
				<div class="container-fluid">
					<div class="row" style="height: 20px;"></div>
					<div class="row mb-3 mx-1">
						<div class="col align-items-center">
							<div id="carouselExampleIndicators" class="carousel slide" data-ride="carousel" data-interval="false">
								<ol class="carousel-indicators">
									<span class="badge rounded-pill bg-warning">{{contents.length}}</span>
									<li v-for="(content, index) in contents" data-target="#carouselExampleIndicators" :data-slide-to="index"></li>
								</ol>
								<div class="carousel-inner" @click="action" @touchend="restartTimeout()">
									<div v-for="content in contents" class="carousel-item" :class="{active: content.isActive}">
										<button v-if="content.ttsInfo.length > 0" class="btn btn-success" style="margin-left: 300px" @click="textToSpeech(content)">
										<i class="icon-speech" style="padding-right: 10px"> 읽어주기
										</button>
										<img class="img-fluid" style="display:block;width: 1000px; height: auto; max-height: 1860px;" :src="contentPath + '/' + content.fileName">
									</div>
								</div>
								<a class="carousel-control-prev" style="filter:progid:DXImageTransform.Microsoft.BasicImage(invert=1)" href="#carouselExampleIndicators" role="button" data-slide="prev" @click="actionPrevNext" @touchend="restartTimeout()">
									<span class="carousel-control-prev-icon" aria-hidden="true"></span>
									<span class="sr-only">이전</span>
								</a>
								<a class="carousel-control-next" style="filter:progid:DXImageTransform.Microsoft.BasicImage(invert=1)" href="#carouselExampleIndicators" role="button" data-slide="next" @click="actionPrevNext" @touchend="restartTimeout()">
									<span class="carousel-control-next-icon" aria-hidden="true"></span>
									<span class="sr-only">다음</span>
								</a>
							</div>
						</div>
					</div>
					<div class="row">
						<div style="float: left;">
							<button class="btn btn-primary" @click="goBack()">
							<i class="icon-arrow-left mx-1">돌아가기
							</button>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
   
</template>

<script>
    import request from '../../../lib/request'

    const ext = window.external,
        PAGE_COUNT = 6,
        AUTO_PAGING_INTERVAL = 10000

    export default {
        data () {
            return {
                client: {
                    boards: []
                },
                page_count: PAGE_COUNT,
                page: 1,
                autoPagingInterval: null,
                autoPagingTimeout: null,
                showPopup : 'none',
                contentPath: '',
                contents: [],
                autoCloseTimeout: null
            }
        },
        methods: {
            showAnnounceDetail (board) {
            	//console.log(board);
                this.stopAutoPaging();
                this.contentPath = board.contentPath
                this.contents = board.contents.map((content, index) => {
                    content.isActive = index === 0
                    let _bigsizeFileName = content.fileName.replace('_b','');
                    content.fileName = _bigsizeFileName.replace('.', '_b.')
                    return content
                })
                this.startAutoCloseTimeout();        
                this.showPopup = 'block';
                /*
                this.$router.push({
                    path: '/template/announce/' + board.contentId
                })
                */
                
            },
            getBoardInfo () {
                request.client.getBoardInfo(this.$clientId, this.$boardIndex)
                    .then(result => {
                        this.$log.debug('get board info ', result)

                        if (result.data.result === 200) {
                            if (/^N$/i.test(result.data.isHolidayOff)) {
                                this.client.boards = result.data.data
                                    .sort((board1, board2) => board2.priority - board1.priority)
                                    .reduce(
                                        (boards, board, index) => {
                                            if (index % PAGE_COUNT === 0) {
                                                // create page arr
                                                boards.push([])
                                            }

                                            if (index % 2 === 0) {
                                                // create row arr
                                                boards[Math.floor(index / PAGE_COUNT)].push([board])
                                            } else {
                                                boards[Math.floor(index / PAGE_COUNT)][boards[Math.floor(index / PAGE_COUNT)].length - 1].push(board)
                                            }

                                            return boards
                                        },
                                        [])

                                this.$log.debug('boards ', this.client.boards)

                                this.startAutoPaging()
                            } else {
                                // 휴일
                                this.$log.debug('휴일')
                            }
                        } else {
                            throw new Error(result.data.resultMsg)
                        }
                    })
                    .catch(err => {
                        alert('데이터 요청을 실패했습니다.\n' + err.message)
                    })
            },
            getPostingEndCount (postingEnd) {
                let year = parseInt(postingEnd.substring(0, 4)),
                    month = parseInt(postingEnd.substring(4, 6)),
                    day = parseInt(postingEnd.substring(6, 8)),

                    date = new Date(year + '-' + month + '-' + day + ' 00:00:00'),

                    diff = Math.abs(date - new Date())

                diff = Math.ceil(diff / (1000 * 3600 * 24))

                return diff
            },
            changePage (page, isAuto) {
                if (this.client.boards[page - 1]) {
                    this.page = page
                } else {
                    this.page = 1
                }

                if (isAuto) {
                    clearTimeout(this.autoPagingTimeout)
                    this.autoPagingTimeout = null
                } else {
                    this.autoPagingTimeout = setTimeout(
                        () => {
                            this.startAutoPaging()
                        },
                        20000)
                }
            },
            startAutoPaging () {
                if (!this.autoPagingInterval) {
                    this.autoPagingInterval = setInterval(
                        () => {
                            this.$log.debug('paging')
                            this.changePage(this.page + 1, true)
                        },
                        AUTO_PAGING_INTERVAL)
                }
            },
            stopAutoPaging () {
                clearInterval(this.autoPagingInterval)
                this.autoPagingInterval = null
            },
            action () {
                this.$log.debug('[AnnounceDetail]action')
                this.startAutoCloseTimeout()
            },
            actionPrevNext () {
                this.$log.debug('[AnnounceDetail]actionPrevNext')
                if (responsiveVoice.isPlaying())
                {
                    this.stopTTS()
                }

                this.startAutoCloseTimeout()
            },
            startAutoCloseTimeout () {
              this.$log.debug('[AnnounceDetail]startAutoCloseTimeout')
                clearTimeout(this.autoCloseTimeout)
                this.autoCloseTimeout = setTimeout(
                    () => {
                        if (responsiveVoice.isPlaying()) {
                            this.startAutoCloseTimeout()
                        } else {
                        	this.goBack();
                        /*
                            this.stopTTS()
                            this.contents = []
                            this.showPopup = 'none';
                            //this.$router.back()
                            */
                        }
                    },
                    30000)
            },
            restartTimeout () {
              this.$log.debug('[AnnounceDetail]restartTimeout')
              this.startAutoCloseTimeout ()
            },
            goBack() {
              this.$log.debug('[AnnounceDetail]goBack')
              clearTimeout(this.autoCloseTimeout)
                this.stopTTS()
                this.contents = []
                this.startAutoPaging();
                this.showPopup = 'none';
              //this.$router.back()
            },
            textToSpeech(content){
               this.$log.debug('[AnnounceDetail]textToSpeech : ', content.ttsInfo)
               responsiveVoice.speak(content.ttsInfo,'Korean Female');
            },
            stopTTS () {
                if (responsiveVoice.isPlaying()) {
                    responsiveVoice.cancel()
                }
            },
            getNotiDetail (contentId) {
                request.noti.searchNotiDetail(contentId)
                    .then(result => {
                    if (result.data.result === 200) {
                        this.contentPath = result.data.data.contentPath
                        setTimeout(function(){
                        this.contents = result.data.data.contents.map((content, index) => {
                            content.isActive = index === 0
                            let _bigsizeFileName = content.fileName
                            content.fileName = _bigsizeFileName.replace('.', '_b.')
                            return content
                        })
                      }.bind(this),500);

                        // alert(this.contents[0].fileName);

                    } else {
                        throw new Error(result.data.resultMsg)
                    }
                })
                .catch(err => {
                    this.$log.error('get client error ', err)
                })
            }
        },
        created () {
            this.getBoardInfo()
        },
        mounted() {
          $(document).ready(function(){
            $(document).bind("contextmenu", function(e) {
              return false;
            });
          });
          $(document)[0].oncontextmenu = function() { return false; }
        }
    }
</script>

<style>
    /*.announce {
        height: 250px;
    }*/
    .carousel-control-next,
    .carousel-control-prev {
      -ms-filter: 'progid:DXImageTransform.Microsoft.BasicImage(invert=1)';
        /* filter: invert(100%); */
    }
</style>
