import remote from './remote'

const URL = {
    GET_LOCATION: '/cms/location',
    GET_ALLOWED_LOCATION: '/cms/location/user',
    GET_ORGAN: '/cms/organ',
    GET_USERS: '/cms/organ/users'
}

export default {
    getLocation () {
        return remote.request({
            url: remote.getUrl(URL.GET_LOCATION)
        })
    },
    getAllowedLocation () {
        return remote.request({
            url: remote.getUrl(URL.GET_ALLOWED_LOCATION)
        })
    },
    getOrgan () {
        return remote.request({
            url: remote.getUrl(URL.GET_ORGAN)
        })
    },
    getUsers (organId) {
        return remote.request({
            url: remote.getUrl(URL.GET_USERS) + '/' + organId
        })
    }
}
