import axios from 'axios'

const remote = {
    //host: '192.168.56.3:9999',
    //host: '172.30.1.20:8080',
    timeout: 10000
},
    METHOD = {
        GET: 'get',
        POST: 'post',
        PUT: 'put',
        DELETE: 'delete'
    }

export default {
    METHOD,
    getUrl (url) {
        return (typeof remote.host === 'string' ? 'http://' + remote.host : '') + url
    },
    request (option) {
        if (typeof option.timeout !== 'number') {
            option.timeout = remote.timeout
        }

        return axios(option)
    },
    // socketIO port : 10000
    wsServer: location.hostname + ':' + 10000
}
