import qs from 'querystringify'
import remote from './remote'

const URL = {
    ADD: '/cms/clients',
    UPDATE: '/cms/clients',
    DELETE: '/cms/clients',
    GET: '/cms/clients?pageCount=50',
    GET_DETAIL: '/cms/clients',
    GET_BOARD_INFO: '/cms/client/board',
    GET_ORGAN_CLIENTS: '/cms/clients/organ',
    GET_SCHEDULES_HOLIDAYS: '/cms/clients/schedule',
    UPDATE_HOLIDAYS: '/cms/clients/holiday',
    UPDATE_SCHEDULES: '/cms/clients/schedule',
    CLIENT_CONTROL: '/cms/control'
}

export default {
    addClient (client) {
        return remote.request({
            url: remote.getUrl(URL.ADD),
            method: remote.METHOD.POST,
            headers: {
                'Content-Type': 'application/json'
            },
            data: client
        })
    },
    updateClient (client) {
        return remote.request({
            url: remote.getUrl(URL.UPDATE) + '/' + client.clientId,
            method: remote.METHOD.PUT,
            headers: {
                'Content-Type': 'application/json'
            },
            data: client
        })
    },
    getClient (clientId) {
        return remote.request({
            url: clientId ? remote.getUrl(URL.GET_DETAIL) + '/' + clientId : remote.getUrl(URL.GET)
        })
    },
    getOrganClients (organId) {
        return remote.request({
            url: remote.getUrl(URL.GET_ORGAN_CLIENTS) + '/' + organId
        })
    },
    getBoardInfo (clientId, boardIndex, templteType, urgentYn) {
        let url = remote.getUrl(URL.GET_BOARD_INFO),
            requestData = {
                clientId: clientId,
                boardIndex: boardIndex
            }

        if (templteType) {
            requestData.templateType = templteType
        }

        if(urgentYn) {
          requestData.urgentYn = urgentYn
        }

        return remote.request({
            url: url + '?' + qs.stringify(requestData),
            timeout: 60000
            // 30000 에서 60초로 늘림
        })
    },
    deleteClient (clientId) {
        return remote.request({
            url: remote.getUrl(URL.DELETE),
            method: remote.METHOD.DELETE,
            headers: {
                'Content-Type': 'application/json'
            },
            data: Array.isArray(clientId) ? clientId : [clientId],
        })
    },
    getSchedulesAndHolidays () {
        return remote.request({
            url: remote.getUrl(URL.GET_SCHEDULES_HOLIDAYS)
        })
    },
    updateSchedules (schedules) {
        return remote.request({
            url: remote.getUrl(URL.UPDATE_SCHEDULES),
            method: remote.METHOD.PUT,
            headers: {
                'Content-Type': 'application/json'
            },
            data: schedules
        })
    },
    updateHolidays (holidays) {
        return remote.request({
            url: remote.getUrl(URL.UPDATE_HOLIDAYS),
            method: remote.METHOD.PUT,
            headers: {
                'Content-Type': 'application/json'
            },
            data: holidays
        })
    },
    controlClient (clientId, action) {
        return remote.request({
            url: remote.getUrl(URL.CLIENT_CONTROL),
            method: remote.METHOD.POST,
            headers: {
                'Content-Type': 'application/json'
            },
            data: {
                clientId: clientId,
                action: action
            }
        })
    }
}
