// 컴포넌트 라우팅 정의
import Vue from 'vue'
import Router from 'vue-router'

import Announce from '../../../components/template/announce/Announce.vue'
import AnnounceDetail from '../../../components/template/announce/AnnounceDetail.vue'

const routes = [
    {
        path: '/template/announce',
        name: 'announce',
        component: Announce
    },
    {
        path: '/template/announce/:contentId',
        name: 'announceDetail',
        component: AnnounceDetail
    },
    // default route
    {
        path: '*',
        redirect: '/template/announce'
    }
]

Vue.use(Router)

export default new Router({
    mode: 'history',
    scrollBehavior: () => ({ y: 0 }),
    routes
})
