<template>
    <div class="app-body">
        <router-view v-if="!isStop" ref="com"></router-view>
    </div>
</template>

<script>
    const ext = window.external

    export default {
        name: 'app',
        data () {
            return {
                isStop: false
            }
        },
        created () {
            this.setSocketEvents()
        },
        methods: {
          callPlayFromApp()
          {
            this.play()
          },
          callStopFromApp()
          {
            this.stop()
          },

            stop () {
                this.isStop = true
                this.$sendClientInfo('N')
                this.$log.debug('stop', this.$refs)
            },
            play () {
                this.isStop = false
                this.$sendClientInfo('Y')
                this.$log.debug('play')
            },
            setSocketEvents () {
                this.$socket
                    .on('poweroff', data => {
                        this.$log.debug('poweroff ', data)

                        try {
                            if (data.clientId == this.$clientId) {
                                ext.systemOff()
                            } else {
                                this.$log.debug('clientId가 맞지 않습니다. ($clientId : ' + this.$clientId + ', data.clientId : ' + data.clientId + ')')
                                this.$socket.emit('templateError', 'clientId가 맞지 않습니다. ($clientId : ' + this.$clientId + ', data.clientId : ' + data.clientId + ')')
                            }
                        } catch (err) {
                            this.$socket.emit('templateError', err.message)
                        }
                    })
                    .on('reboot', data => {
                        this.$log.debug('reboot ', data)

                        if (data.clientId == this.$clientId) {
                            ext.systemReboot()
                        } else {
                            this.$log.debug('clientId가 맞지 않습니다. ($clientId : ' + this.$clientId + ', data.clientId : ' + data.clientId + ')')
                        }
                    })
                    .on('play', data => {
                        this.$log.debug('play ', data)

                        if (data.clientId == this.$clientId) {
                            ext.playEvent(this.boardIndex)
                        } else {
                            this.$log.debug('clientId가 맞지 않습니다. ($clientId : ' + this.$clientId + ', data.clientId : ' + data.clientId + ')')
                        }

                        // if (data.clientId == this.$clientId) {
                        //     this.play()
                        // } else {
                        //     this.$log.debug('clientId가 맞지 않습니다. ($clientId : ' + this.$clientId + ', data.clientId : ' + data.clientId + ')')
                        // }
                    })
                    .on('stop', data => {
                        this.$log.debug('stop ', data)

                        if (data.clientId == this.$clientId) {
                            ext.stopEvent(this.boardIndex)
                        } else {
                            this.$log.debug('clientId가 맞지 않습니다. ($clientId : ' + this.$clientId + ', data.clientId : ' + data.clientId + ')')
                        }
                        // if (data.clientId == this.$clientId) {
                        //     this.stop()
                        // } else {
                        //     this.$log.debug('clientId가 맞지 않습니다. ($clientId : ' + this.$clientId + ', data.clientId : ' + data.clientId + ')')
                        // }
                    })
                    .on('getLocalIp', data => {
                        this.$log.debug('getLocalIp')

                        try {
                            if (data.clientId == this.$clientId) {
                                this.$socket.emit('getLocalIp', ext.getLocalIp())
                            } else {
                                this.$log.debug('clientId가 맞지 않습니다. ($clientId : ' + this.$clientId + ', data.clientId : ' + data.clientId + ')')
                                this.$socket.emit('templateError', 'clientId가 맞지 않습니다. ($clientId : ' + this.$clientId + ', data.clientId : ' + data.clientId + ')')
                            }
                        } catch (err) {
                            this.$socket.emit('templateError', err.message)
                        }
                    })

                this.$log.debug('set socket events')
            }
        }
    }
</script>

