import '@babel/polyfill'
import Vue from 'vue'
import io from 'socket.io-client'
import qs from 'querystringify'

import '../../../lib/logger'

import router from './router'

import remote from '../../../lib/request/remote'

import Container from './Container.vue'

Vue.use({
    install: function (Vue, options) {
        let parsedQs = qs.parse(location.search),
            clientId = parsedQs.clientId,
            boardIndex = parsedQs.boardIndex

        console.log('clientId :', clientId, ', boardIndex :', boardIndex, ', wsServer :', remote.wsServer)

        Vue.prototype.$clientId = clientId
        Vue.prototype.$boardIndex = boardIndex

        Vue.prototype.$socket = io(remote.wsServer)
            .on('connect', () => {
                console.log('connect')

                Vue.prototype.$socket.emit(
                    'clientInfo',
                    {
                        clientId: clientId,
                        boardIndex: boardIndex
                    })
            })

        Vue.prototype.$sendClientInfo = function (activeYn) {
            console.log('sendClientInfo', activeYn)
            Vue.prototype.$socket.emit(
                'boardStatus',
                {
                    clientId: clientId,
                    boardIndex: boardIndex,
                    activeYn: activeYn
                })
        }
    }
})

window.templateVue = new Vue({
    el: '#app-container',
    render (h) {
        return h(Container)
    },
    router
})
